<template>
  <div class="app-container">
    <el-card v-loading="loading">
      <table-tool
        :search-columns="searchColumns"
        :add-able="addAble"
        :export-able="true"
        @on-search="handleSearch"
        @on-reset="handleReset"
        @on-add="handleAdd"
      />

      <el-table :data="list" fit size="medium">
        <el-table-column prop="id" label="序号" width="100px" />
        <el-table-column prop="name" label="标识" />
        <el-table-column prop="display_name" label="名称" />
        <el-table-column
          prop="permissions"
          label="权限"
          :show-overflow-tooltip="true"
          align="center"
        >
          <template slot-scope="scope">
            <el-tag
              v-for="(permission, index) in scope.row.permissions"
              :key="index"
              type="primary"
              size="mini"
              style="margin-right: 2px;"
              disable-transitions
              >{{ permission.display_name }}</el-tag
            >
          </template>
        </el-table-column>
        <el-table-column prop="created_at" label="创建时间" />
        <el-table-column align="center" label="操作">
          <template slot-scope="scope">
            <el-button
              v-if="showAble"
              size="mini"
              @click.native="handleShow(scope.row)"
              >查看</el-button
            >
            <el-button
              v-if="editAble"
              type="primary"
              size="mini"
              @click.native="handleEdit(scope.row)"
              >编辑</el-button
            >

            <el-popover
              v-if="deleteAble"
              :ref="`delete-${scope.row.id}`"
              placement="top"
            >
              <p>确定删除吗？</p>
              <el-button size="mini" @click="cancleDelete(scope.row.id)"
                >取消</el-button
              >
              <el-button
                size="mini"
                type="primary"
                @click="handleDelete(scope.row.id)"
                >确认</el-button
              >
              <el-button
                type="danger"
                size="mini"
                slot="reference"
                style="margin-left: 10px;"
                >删除</el-button
              >
            </el-popover>
          </template>
        </el-table-column>
      </el-table>

      <app-pagination
        v-if="total > 0"
        :total="total"
        :page.sync="query.page"
        :limit.sync="query.limit"
        @pagination="handleGet"
      />
    </el-card>

    <el-dialog
      :visible.sync="dialogVisible"
      :title="`${dialogTitle}角色`"
      @closed="handleClosed"
      :close-on-click-modal="false"
      :close-on-press-escape="false"
    >
      <el-form
        ref="dataForm"
        :rules="formRules"
        :model="formData"
        label-width="80px"
        label-position="right"
      >
        <el-form-item label="角色名称" prop="display_name">
          <el-col :span="20">
            <el-input
              autocomplete="off"
              v-model="formData.display_name"
              placeholder="请输入角色名称"
            ></el-input>
          </el-col>
        </el-form-item>

        <el-form-item label="角色标识" prop="name">
          <el-col :span="20">
            <el-input
              autocomplete="off"
              v-model="formData.name"
              placeholder="请输入角色标识"
            ></el-input>
          </el-col>
        </el-form-item>

        <el-form-item label="权限" prop="permissions">
          <el-col :span="20">
            <transfer
              filterable
              :data="allPermissions"
              :titles="['权限', '已选权限']"
              v-model="formData.permissions"
            >
            </transfer>
          </el-col>
        </el-form-item>
      </el-form>
      <div slot="footer">
        <el-button @click.native="handleCancle">取 消</el-button>
        <el-button
          v-if="isEdit"
          type="primary"
          :loading="storeLoading"
          @click="handleUpdate"
          >确 定</el-button
        >
        <el-button
          v-else
          type="primary"
          :loading="storeLoading"
          @click="handleStore"
          >确 定</el-button
        >
      </div>
    </el-dialog>

    <el-dialog ref="dataShow" :visible.sync="showVisible" title="角色详情">
      <el-form label-width="80px" label-position="right">
        <el-form-item label="角色名称:" prop="display_name">
          {{ item.display_name }}
        </el-form-item>

        <el-form-item label="角色标识:" prop="display_name">
          {{ item.name }}
        </el-form-item>

        <el-form-item label="权限:" prop="display_name">
          <el-tag
            v-for="(permission, index) in item.permissions"
            :key="index"
            type="primary"
            style="margin-right: 10px;"
            disable-transitions
            >{{ permission.display_name }}</el-tag
          >
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>

<script>
import store from "@/store";
import { Message, Transfer } from "element-ui";
import { createNamespacedHelpers } from "vuex";
const { mapState, mapGetters, mapActions } = createNamespacedHelpers("role");
import TableTool from "@/components/TableTool";
import AppPagination from "@/components/Pagination";
import ListMixin from "@/mixins/ListMixin";
import ListSearchMixin from "@/mixins/ListSearchMixin";
import SearchResetMixin from "@/mixins/SearchResetMixin";
import CreateMixin from "@/mixins/CreateMixin";
import StoreMixin from "@/mixins/StoreMixin";
import DeleteMixin from "@/mixins/DeleteMixin";
import EditMixin from "@/mixins/EditMixin";
import UpdateMixin from "@/mixins/UpdateMixin";
import ShowMixin from "@/mixins/ShowMixin";
import { checkPermission } from "@/utils/permission";
export default {
  name: "Role",
  data() {
    return {
      query: {
        page: 1,
        limit: 20
      },
      searchColumns: [
        { title: "角色标识", name: "name" },
        { title: "角色名称", name: "display_name" }
      ],
      formRules: {
        name: [{ required: true, message: "请填写角色标识", trigger: "blur" }],
        display_name: [
          { required: true, message: "请填写角色名称", trigger: "blur" }
        ],
        permissions: [
          { required: true, message: "请选择权限", trigger: "blur" }
        ]
      },
      formData: {
        id: 0,
        name: "",
        display_name: "",
        permissions: []
      },
      isEdit: false,
      allPermissions: []
    };
  },
  async beforeRouteEnter(to, from, next) {
    try {
      await store.dispatch("role/get");
      const response = await store.dispatch("permission/all");
      next(vm => {
        vm.setPermissions(response.data);
      });
    } catch (e) {
      Message.error(e.response.data.message);
    }
  },
  components: {
    TableTool,
    AppPagination,
    Transfer
  },
  mixins: [
    ListMixin,
    ListSearchMixin,
    SearchResetMixin,
    CreateMixin,
    StoreMixin,
    EditMixin,
    EditMixin,
    DeleteMixin,
    UpdateMixin,
    ShowMixin
  ],
  methods: {
    ...mapActions(["get", "store", "delete", "update"]),
    setPermissions(permissions) {
      this.allPermissions = permissions;
    },
    handleEdit(row) {
      this.formData.id = row.id;
      this.formData.name = row.name;
      this.formData.display_name = row.display_name;
      this.formData.permissions = row.permissions.map(item => {
        return item.id;
      });
      this.dialogTitle = "编辑";
      this.dialogVisible = true;
      this.isEdit = true;
    }
  },
  computed: {
    ...mapState({
      list: state => state.list
    }),
    ...mapGetters(["total"]),
    showAble: () => {
      return checkPermission("roles-show");
    },
    addAble: () => {
      return checkPermission("roles-create");
    },
    editAble: () => {
      return checkPermission("roles-edit");
    },
    deleteAble: () => {
      return checkPermission("roles-destroy");
    }
  },
  mounted() {
    this.query = {
      page: 1,
      limit: 20
    };
  }
};
</script>

<style lang="scss" scoped></style>
